.footer{
  padding-top: 3.75em;
  padding-bottom: 3.25em;
  &__top{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 3em 2.5em;
  }
  &__logo{
    display: block;
    width: 11.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__middle{
    margin-top: 5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__c,
  &__link{
    font-size: 1em;
    line-height: 131%;
    margin-right: 2.5em;
  }
  &__pay{
    display: flex;
    &-item{
      height: 1.5em;
      margin-right: .75em;
      &:last-child{
        margin-right: 0;
      }
      img{
        height: 100%;
        display: block;
      }
    }
  }
  &__bottom{
    margin: 1.5em auto 0;
    padding-top: 1.5em;
    border-top: 1px solid $gray;
    .container{
      display: flex;
      justify-content: center;
    }
  }
  &__catalog{
    display: flex;
    align-items: center;
    font-weight: 600;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    span{
      font-size: 1.375em;
    }
    svg{
      width: 1.75em;
      margin-left: 1.25em;
    }
  }
}

.footer-group{
  &__top{
    min-height: 5em;
    border-bottom: 1px solid $gray;
    margin-bottom: 1.5em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1.125em;
    line-height: 133%;
    margin-bottom: 1.11em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__stores{
    display: flex;
    &-item{
      margin-right: 1.5em;
      height: 2.5em;
      img{
        width: auto;
        height: 100%;
        display: block;
      }
      &:last-child{
        margin: 0;
      }
    }
  }
}

.footer-contacts{
  @include grid100gap(1em);
  &__item{
    @include grid100gap(.25em);
  }
  &__title{
    font-weight: 600;
    font-size: 1.125em;
    line-height: 133%;
    margin-bottom: .22em;
  }
  &__desc{
    line-height: 131%;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 1.5em;
    padding-bottom: 1.25em;
    &__top{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
    &__middle{
      margin-top: 2em;
    }
    &__c,
    &__link{
      font-size: .875em;
      line-height: 136%;
    }
    &__bottom{
      width: calc(100% - 2.5em);
      margin-top: 1em;
      padding-top: 1em;
      .container{
        padding: 0;
      }
    }
    &__catalog{
      span{
        font-size: 1em;
      }
      svg{
        margin-left: 1em;
      }
    }
  }

  .footer-group{
    &__top{
      min-height: 4.75em;
      margin-bottom: 1em;
    }
    &__link{
      font-size: .875em;
      line-height: 136%;
      margin-bottom: .86em;
    }
    &__stores{
      &-item{
        margin-right: 1em;
        height: 2em;
      }
    }
  }

  .footer-contacts{
    &__item{
      gap: .5em;
    }
    &__title{
      font-size: 1em;
      margin-bottom: .125em;
    }
    &__desc{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      grid-template-columns: 100%;
      text-align: center;
    }
    &__logo{
      margin: 0 auto;
    }
    &__middle{
      flex-direction: column;
    }
    &__items{
      margin-bottom: .75em;
      width: 100%;
      justify-content: space-between;
    }
    &__c,
    &__link{
      margin: 0;
    }
  }

  .footer-group{
    &__top:not(._big){
      min-height: 3em;
    }
    &__links{
      align-items: center;
    }
    &__stores,
    .socials{
      justify-content: center;
    }
  }
}
