@import "../vars.scss";
.breadcrumbs {
    padding: 2em 0 2.5em;
    li{
        position: relative;
        display: inline;
        color: $gray;
        &:last-child {
            pointer-events: none;
            color: $black;
        }
        &:not(:first-child) {
            margin-left: 3.25em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1.625em;
                height: .625em;
                top: .25em;
                right: 100%;
                margin-right: 1em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
            }
        }
    }
    a{
        font-size: 1em;
        line-height: 131%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1.25em 0 2em;
        li{
            font-size: .875em;
        }
    }
}


