.partners{
  padding-top: 0;
  overflow: hidden;
  position: relative;
  &__grid{
    margin-top: 3em;
    row-gap: 3em;
  }
  &__toy{
    position: absolute;
    width: 10.5em;
    top: 10.75em;
    right: -1.25em;
    transform: rotate(-164deg);
  }
}

.partner-card{
  .text{
    margin-top: .9em;
  }
}

.reg{
  padding-top: 0;
  overflow: hidden;
  position: relative;
  &__toy{
    position: absolute;
    width: 18em;
    top: 9.75em;
    left: 9.375em;
    transform: rotate(164deg);
  }
  &__toy2{
    position: absolute;
    width: 12.75em;
    bottom: 17em;
    right: 13em;
    transform: rotate(-164deg);
  }
  .contacts-form{
    text-align: center;
  }
  @media screen and (min-width: 577px){
    .contacts-form{
      padding: 2em 3em 3em;
      width: 47.5em;
      margin: 0 auto;
    }
  }
}


@media screen and (max-width: 992px){
  .partners{
    &__grid{
      margin-top: 2em;
      row-gap: 1.5em;
    }
    &__toy{
      display: none;
    }
  }

  .partner-card{
    .text{
      margin-top: 1.12em;
    }
  }

  .req{
    &__toy,
    &__toy2{
      display: none;
    }
  }
}


