.header{
  &__inner{
    position: relative;
  }
  &__logo{
    position: absolute;
    top: 1.5em;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    flex-shrink: 0;
    width: 11.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.mob-menu{
  &__top{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 2.625em 0 2.125em;
  }
  .socials{
    margin-right: auto;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 1.5em;
    border-top: 1px solid $gray;
  }
  &__btn{
    font-weight: 500;
    height: 3.375em;
    padding: 0 2em;
    flex-shrink: 0;
    img{
      width: 1.75em;
      margin-left: .75em;
    }
  }
}

.header-lk{
  display: flex;
  margin-left: 2em;
  &__btn{
    font-weight: 500;
    height: 3.5em;
    padding: 0 2em;
    margin-right: 1em;
    &:last-child{
      margin: 0;
    }
    span{
      font-size: 1.125em;
    }
  }
}

.country-select{
  min-width: 14.25em;
  position: relative;
  z-index: 5;
  &__show{
    padding: 0 1.25em;
    display: flex;
    align-items: center;
    border-radius: .75em;
    border: 1px solid $black;
    height: 3.5em;
    cursor: pointer;
    position: relative;
    z-index: 2;
    svg{
      width: 1.125em;
      margin-left: auto;
    }
  }
  &__flag{
    width: 1.875em;
    margin-right: .5em;
  }
  &__current{
    font-size: 1.125em;
    line-height: 133%;
    padding-right: .25em;
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: .75em;
    border: 1px solid $primary;
    padding: 3.75em 1.25em 1em;
    background-color: $white;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    padding-top: 1.25em;
    border-top: 1px solid $gray;
  }
  &__item{
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    &:last-child{
      margin-bottom: 0;
    }
    span{
      font-size: 1.125em;
      line-height: 133%;
    }
  }
  &._open{
    .country-select__show{
      border-color: transparent;
      svg{
        transform: rotate(-180deg);
        path{
          stroke: $gray;
        }
      }
    }
    .country-select__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  margin: 0 6.25em;
  &__link{
    font-size: 1.375em;
    line-height: 132%;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 1.25em;
    &__inner{
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__logo{
      z-index: 11;
      position: relative;
      top: unset;
      left: unset;
      transform: unset;
      width: 7.625em;
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.625em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .mob-menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: $white;
    height: $vh;
    width: 100%;
    padding-top: 4em;
    &__inner{
      padding: 7em 1.25em 1.25em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
      position: relative;
    }
    &__top,
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
    }
    &__bottom{
      order: -1;
      border: none;
      margin-bottom: 1em;
    }
    &__btn{
      width: 100%;
      max-width: 22em;
      margin-top: 1em;
    }
    .socials{
      margin-top: 1em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    order: -1;
    margin: 0;
    &__link{
      margin: 0 0 1em;
      &:last-child{
        margin: 0;
      }
    }
  }

  .header-lk{
    order: -1;
    margin-left: 0;
    width: 100%;
    max-width: 22em;
    &__btn{
      flex-grow: 1;
    }
  }

  .country-select{
    margin-top: 1em;
    width: calc(100% - 2.5em);
    max-width: 22em;
    position: absolute;
    top: 1em;
    left: 1.25em;
  }
}


