.what-is{
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 39.25em calc(100% - 41.75em);
    gap: 2.5em;
    margin-top: 2.5em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .75em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__toy{
    position: absolute;
    top: 9em;
    left: -3.125em;
    width: 14.25em;
    transform: rotate(164deg);
    @media screen and (max-width: 1900px){
      top: unset;
      bottom: 2em;
    }
  }
}

.news{
  overflow: hidden;
  position: relative;
  &__toy{
    position: absolute;
    top: 7.5em;
    right: -.75em;
    width: 10.5em;
    transform: rotate(164deg);
  }
}

.news-slider{
  .slider__wrap{
    overflow: unset;
  }
  .swiper-slide{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  .swiper-slide-active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px){
    .swiper-slide-next,
    .swiper-slide-next+.swiper-slide,
    .swiper-slide-next+.swiper-slide+.swiper-slide{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.blog-card{
  border-radius: .75em;
  height: 100%;
  box-shadow: $shadow;
  background-color: $white;
  &__img{
    @include imgCover(63.9%);
    border-radius: .75em .75em 0 0;
  }
  &__content{
    padding: 1.25em;
  }
  &__date{
    margin-bottom: .5em;
  }
}


@media screen and (max-width: 992px){
  .what-is{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1em;
    }
    &__toy{
      display: none;
    }
  }

  .news{
    &__toy{
      display: none;
    }
  }

  .news-slider{
    @media screen and (min-width: 577px){
      .swiper-slide{
        width: 50%;
      }
      .swiper-slide-next{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .blog-card{
    &__content{
      padding: 1em;
    }
    &__date{
      margin-bottom: .67em;
    }
  }
}


@media screen and (max-width: 576px){
  .what-is{
    &__inner{
      grid-template-columns: 100%;
      text-align: center;
    }
    &__title{
      padding: 0 1em;
    }
    &__btn{
      width: 100%;
    }
  }
}
