@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.m-section{
  padding: 3.75em 0;
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 3.5em;
    height: 3.5em;
    border-radius: 50%;
    border: .09375em solid $orange;
    margin-right: 2em;
    @include center;
    &:last-child{
      margin: 0;
    }
    &:hover{
      background-color: $orange;
      path{
        fill: $white;
      }
      ._stroke{
        fill: none;
        stroke: $white;
      }
    }
    svg{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.5em 0;
  }

  .socials{
    &__item{
      width: 2em;
      height: 2em;
      border-width: 1px;
      margin-right: 1em;
    }
  }
}


