.contacts{
  position: relative;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
    position: relative;
    z-index: 2;
  }
  &__toy{
    position: absolute;
    top: 11em;
    left: -.3125em;
    width: 10.5em;
    transform: rotate(-14deg);
  }
  &__toy2{
    position: absolute;
    bottom: 0;
    right: -1.25em;
    width: 15em;
    transform: rotate(17deg);
  }
}

.shops{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2em 2.5em;
    margin-top: 2em;
  }
  .text{
    font-weight: 600;
    margin-top: 1.33em;
    p{
      @include lastM(0 0 1.33em);
    }
  }
}

.shop-card{
  border: 2px dashed #ff9103;
  background: $white;
  padding-bottom: 44%;
  &._votonya{
    border-color: #e5097f;
  }
  &._wb{
    border-color: #d93999;
  }
  &._ozon{
    border-color: #0069ff;
  }
}

.contacts-form{
  box-shadow: $shadow;
  padding: 2em 3em 3em;
  background-color: $white;
  border-radius: .75em;
  &__fields{
    @include grid100gap(1.5em);
    margin-top: 2em;
  }
  &__btn{
    width: 100%;
    margin-top: 2em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2em 1.25em;
    }
    &__toy,
    &__toy2{
      display: none;
    }
  }

  .shops{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
      margin-top: 1.25em;
    }
    .text{
      margin-top: 1.25em;
      font-size: 1em;
      p{
        @include lastM(0 0 1.25em);
      }
    }
  }

  .shop-card{
    padding-bottom: 47%;
    .brand-card__img{
      padding: 1em 1.5em;
    }
  }

  .contacts-form{
    padding: 1em 1.25em;
    &__fields{
      gap: 1em;
      margin-top: 1em;
    }
    &__btn{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .shops{
    text-align: center;
  }

  .contacts-form{
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
  }
}
