.main{
  position: relative;
  padding-top: 3em;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 50em calc(100% - 50em);
    border-radius: .75em;
    background-color: $blue;
    color: $white;
    overflow: hidden;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      border-radius: 50%;
      filter: blur(30em);
      width: 32em;
      height: 32em;
      background-color: $white;
      top: -7.75em;
      left: -8.5em;
      z-index: 2;
    }
  }
  &__content{
    padding: 3em 4em;
  }
  &__img{
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 0 .75em .75em 0;
    }
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__title{
    max-width: 8.5em;
  }
  &__toy{
    position: absolute;
    top: 3em;
    left: -.3125em;
    width: 10.125em;
    transform: rotate(-14deg);
  }
  &__toy2{
    position: absolute;
    bottom: 0;
    right: -1.25em;
    width: 13em;
    transform: rotate(17deg);
  }
}

.swiper-button{
  width: 4em;
  height: 4em;
  border-radius: 50%;
  @include center;
  margin: 0;
  img{
    width: 1em;
  }
}

.news-slider,
.brands-slider{
  margin-top: 2.5em;
  padding: 0 6em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
    border-radius: .75em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 25%;
      padding-right: 1.5em;
    }
    &-button{
      transform: translateY(-50%);
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.brand-card{
  padding-bottom: 49.4%;
  position: relative;
  background: #b8599d;
  border-radius: .75em;
  display: block;
  &._pink{
    background: #ee73aa;
  }
  &._orange{
    background: #fac7b2;
  }
  &._blue{
    background: #312883;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    padding: 1.75em 3em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-top: 2em;
    &__inner{
      grid-template-columns: 50% 50%;
    }
    &__content{
      padding: 1.5em 1.25em;
    }
    &__desc{
      margin-top: .75em;
    }
    &__toy,
    &__toy2{
      display: none;
    }
  }

  .swiper-button{
    width: 2em;
    height: 2em;
    img{
      width: .625em;
    }
  }

  .news-slider,
  .brands-slider{
    margin-top: 1em;
    padding: 0 3em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: 1em;
      }
    }
  }

  .brand-card{
    &__img{
      padding: 1.25em 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__inner{
      grid-template-columns: 100%;
      text-align: center;
    }
    &__img{
      img{
        border-radius: 0 0 .75em .75em;
      }
    }
  }

  .news-slider,
  .brands-slider{
    .swiper-slide{
      width: 100%;
    }
  }
}
