.text{
  ul,
  p{
    line-height: inherit;
    @include lastM(0 0 1em);
  }
  li{
    line-height: inherit;
  }
  a{
    color: $primary;
  }
  ul,ol{
    @include grid100gap(.9em);
    li{
      position: relative;
      padding-left: 2.36em;
      &:before{
        content: '';
        position: absolute;
        width: 1.63em;
        height: 1.63em;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        @include center;
        color: $white;
      }
    }
  }
  ul{
    li{
      &:before{
        background-image: url(../img/li-icon.svg);
        @include bgImgSize(100%);
      }
    }
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list);
        background-color: $primary;
        border-radius: 50%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    font-size: .875em;
    ol,
    ul{
      gap: .86em;
      li{
        padding-left: 2.86em;
        &:before{
          width: 1.71em;
          height: 1.71em;
        }
      }
    }
  }
}


