.shops-page{
  padding-top: 0;
  &__inner{
    position: relative;
  }
}

.map{
  height: 43.75em;
  margin-top: 2.5em;
  border-radius: .75em;
  overflow: hidden;
}

.shops-nav{
  position: absolute;
  top: 3.75em;
  right: 8.3125em;
  border-radius: .75em;
  background-color: $white;
  box-shadow: $shadow;
  padding: 2em 1.5em 7.5em;
  width: 31em;
  text-align: center;
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1.25em);
  }
  .fg{
    select{
      padding-left: 1em;
      padding-right: 2em;
      height: 3em;
    }
  }
  &__variants{
    display: none;
    &._active{
      display: block;
    }
  }
}

.shops-brands{
  margin-top: 5em;
  .brand-card{
    img{
      flex-grow: 1;
    }
  }
}


@media screen and (max-width: 992px){
  .map{
    margin-top: 2em;
  }

  .shops-nav{
    top: 1em;
    right: 1em;
    padding: 1.25em 1em 3em;
    width: 19em;
    &__fields{
      margin-top: 1.25em;
      gap: 1em;
    }
    .fg{
      select{
        font-size: .875em;
        height: 3.65em;
        padding-left: 1.75em;
        padding-right: 3em;
      }
    }
  }

  .shops-brands{
    margin-top: 3em;
  }
}


@media screen and (max-width: 576px){
  .shops-nav{
    top: unset;
    bottom: 1em;
  }

  .map{
    height: 33.5em;
  }

  .shops-brands{
    gap: 1em;
  }
}
