.brands-page{
  padding-top: 0;
  position: relative;
  overflow: hidden;
  .about__items{
    gap: 3.75em;
  }
  .brand-card{
    &__img{
      padding: 4em 8em;
    }
  }
  .what-is__toy{
    top: 2em;
  }
  .main__toy2{
    right: unset;
    left: -1em;
    transform: rotate(-17deg);
  }
  .news__toy{
    top: unset;
    bottom: 40em;
    transform: rotate(200deg);
  }
}


@media screen and (max-width: 992px){
  .brands-page{
    .about__items{
      gap: 3em;
    }
    .brand-card{
      &__img{
        padding: 1.75em 3.5em;
      }
    }
    .about-card__desc{
      margin-top: 1em;
    }
  }
}


