.contacts-page{
  padding-top: 0;
  &__grid{
    margin-top: 2.5em;
  }
}

.contact-card{
  padding: 1.5em 2em;
  background-color: $white;
  box-shadow: $shadow;
  border-radius: .75em;
  &__title{
    color: $green;
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
  }
  &._red{
    .contact-card__title{
      color: $primary;
    }
    .contact-item__icon{
      path{
        fill: $primary;
      }
    }
  }
  &._blue{
    .contact-card__title{
      color: $blue;
    }
    .contact-item__icon{
      path{
        fill: $blue;
      }
    }
  }
}

.contact-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 2em;
    flex-shrink: 0;
    @include center;
    margin-right: 1em;
    svg{
      width: 100%;
    }
  }
  &__text{
    font-size: 1.375em;
    line-height: 132%;
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    &__grid{
      margin-top: 2em;
    }
  }

  .contact-card{
    padding: 1.25em;
    &__title{
      font-size: 2em;
    }
    &__items{
      margin-top: 1.25em;
      gap: 1.25em;
    }
  }

  .contact-item{
    &__text{
      font-size: .875em;
      line-height: 136%;
    }
  }
}


