@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3em;
    border-radius: .75em;
    height: 4.375em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.375em;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-purple{
    background-color: $purple;
    color: $white;
    &:hover{
        background-color: darken($purple,10%);
        color: $white;
    }
}

.m-btn-orange{
    background-color: $orange;
    color: $white;
    &:hover{
        background-color: darken($orange,10%);
        color: $white;
    }
}

.m-btn-orange-outline{
    border: 1px solid $orange;
    color: $orange;
    &:hover{
        background-color: $orange;
        color: $white;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.375em;
        padding: 0 2em;
        span{
            font-size: 1em;
        }
    }
}
