.about{
  padding-top: 0;
  overflow: hidden;
  position: relative;
  &__items{
    @include grid100gap(7.5em);
    position: relative;
    z-index: 2;
  }
  .main__toy2{
    top: 29.5em;
    bottom: unset;
  }
  .what-is__toy{
    top: 64.5em;
  }
  .news__toy{
    top: unset;
    bottom: 5em;
  }
  @media screen and (max-width: 1900px){
    .main__toy{
      top: 28em;
    }
  }
}

.about-card{
  .h2{
    max-width: 14em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .75em;
    }
  }
  &__desc{
    margin-top: 2em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .about-card__col2{
        order: -1;
      }
    }
  }
}

.advantages{
  &__grid{
    margin-top: 2.5em;
  }
}

.advantage-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: $white;
  border-radius: .75em;
  box-shadow: $shadow;
  padding: 1.5em 1.5em 2em;
  &__icon{
    height: 6.25em;
    margin-bottom: 1.25em;
    flex-shrink: 0;
    img{
      height: 100%;
      display: block;
    }
  }
}

.mission{
  &__desc{
    font-weight: 600;
    margin-top: 1.5em;
  }
  &__desc2{
    margin-top: 1em;
  }
  &__img{
    margin-top: 2em;
    img{
      width: 100%;
      display: block;
      border-radius: .75em;
    }
  }
  &__items{
    @include grid100gap(1.5em);
  }
}

.qa-card{
  transition: all .3s;
  border-radius: .75em;
  border: 1px solid $primary;
  &__show{
    cursor: pointer;
    padding: 1em 1em 1em 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__hidden{
    padding: .25em 1em 1em 1.5em;
    display: none;
    color: $white;
  }
  &__title{
    font-weight: 600;
  }
  &__btn{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    background-color: $primary;
    flex-shrink: 0;
    margin-left: 1em;
    @include center;
    svg{
      width: 1.125em;
    }
  }
  .text{
    font-size: 1.125em;
    line-height: 133%;
  }
  &._open{
    background-color: $primary;
    color: $white;
    .qa-card__btn{
      background-color: $white;
      svg{
        transform: rotate(-180deg);
      }
      path{
        stroke: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    padding-top: 0;
    &__items{
      @include grid100gap(7.5em);
    }
  }

  .about-card{
    &__desc{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .advantages{
    &__grid{
      margin-top: 1.25em;
    }
  }

  .advantage-card{
    padding: 1.5em .625em 1.25em;
    &__icon{
      height: 5em;
      margin-bottom: 1em;
    }
  }

  .mission{
    &__desc{
      margin-top: 1em;
    }
    &__desc2{
      margin-top: .75em;
    }
    &__img{
      margin-top: 1em;
    }
  }

  .qa-card{
    &__show{
      cursor: pointer;
      padding: .75em 1em;
    }
    &__hidden{
      padding: .25em 1em .75em;
    }
    &__title{
      font-size: 1em;
    }
    &__btn{
      width: 2em;
      height: 2em;
      svg{
        width: 1em;
      }
    }
    .text{
      font-size: .875em;
      line-height: 136%;
    }
  }
}


@media screen and (max-width: 576px){
  .about-card{
    text-align: center;
    &__btn{
      width: 100%;
    }
  }

  .advantages{
    &__grid{
      gap: 1em;
    }
  }

  .mission{
    &__col{
      text-align: center;
    }
  }
}
